




























import Vue from 'vue';

export default Vue.extend({
    name: 'ToDoCard',
    data() {
        return {
            toDoList: [],
        };
    },

    props: {
        todo: {
            type: Array,
        },
    },

    methods: {
        deleteToDo(index: number) {
            this.$emit('delete');
            this.todo.splice(index - 1, 1);
        },
    },

    computed: {},
});
